* {
  font-family: 'Montserrat Alternates', sans-serif;
}


.nav {
	border-bottom: 5px solid #caff00;
	width: 100%;
	position: fixed;
	top:0

}

.signout {

	margin-left: 85%;
}

.logout {
	margin-left: 70%;
}

.navbar {
	overflow: hidden;
  

}

.navbar li {
	float: left;
  font-size: 14px;
  color: #39FF14;
  text-align: center;
  padding: 8px 10px;
  text-decoration: none
}

.dropdown {
  float: left;
  overflow: hidden;
}

.dropdown .dropbtn {
  font-size: 16px;
  border: none;
  outline: none;
  color: #39FF14;
  padding: 10px 12px;
  background-color: inherit;
  font-family: inherit; /* Important for vertical align on mobile phones */
  margin: 0; /* Important for vertical align on mobile phones */
}

.navbar li:hover, .dropdown:hover .dropbtn {
  background-color: black;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f0f0f0;
  min-width: 400px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  
}

.dropdown-content a {
  float: none;
  color: #39FF14;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.dropdown-content a:hover {
  background-color: black;
}


.dropdown:hover .dropdown-content {
  display: block;
}

ul {
	list-style-type: none;
	
}



a {
	text-decoration: none;
  color: black
}

.signout {
  display: block;
  text-decoration: none;
  float: right;
  color:black
}

a:hover {
    color:#39FF14; 
    text-decoration:none; 
    cursor:pointer;  
}

